import React, { useState, useRef } from "react";
import { Link } from "react-router-dom";
import {
  contactInfoContent,
  EMAIL,
  WEBSITE,
  PHONE,
  CONTACT,
} from "../../utils/exportContent";

const Footer = () => {
  const msgContainerRef = useRef();

  const [details, setDetails] = useState({
    name: "",
    email: "",
    message: "",
  });

  const changeHandler = (e) =>
    setDetails({ ...details, [e.target.name]: e.target.value });

  const gotopage = () => {
    const position = msgContainerRef.current.offsetTop - 30;
    window.scrollTo({
      top: position,
      left: 0,
      behavior: "smooth",
    });
  };

  return (
    <div style={contaninerStyle}>
      <div className="footer-welcome">
        <div>
          <h1>Let’s work together to</h1>
          <h1>build something great.</h1>
          <div className="goto_page" onClick={gotopage}>
            <p>Say Hello</p>
            <div className="bg"></div>
          </div>
        </div>
      </div>

      <div id="contact" ref={msgContainerRef} className="footer_container">
        <div className="msg-form-con">
          <div style={mailIcon}>
            <i className="fa fa-envelope-o" aria-hidden="true"></i>
          </div>
          <div className="container">
            <h1 style={{ textTransform: "uppercase" }}>Let's get in touch.</h1>
            <p style={paraContainer}>
              IF YOU ANY QUESTIONS, REQUESTS OR INQUIRIES FOR US, JUST ASK. WE
              WANT TO HEAR THEM
            </p>
            <div style={inputConStyle}>
              <label style={labelStyle}>Name</label>
              <input
                name="name"
                type="text"
                style={inputField}
                autoComplete="off"
                value={details["name"]}
                onChange={(e) => changeHandler(e)}
              />
            </div>
            <div style={inputConStyle}>
              <label style={labelStyle}>Email</label>
              <input
                name="email"
                type="email"
                style={inputField}
                autoComplete="off"
                value={details["email"]}
                onChange={(e) => changeHandler(e)}
              />
            </div>
            <div style={inputConStyle}>
              <label style={labelStyle}>Your Message</label>
              <textarea
                name="message"
                style={textareaStyle}
                value={details["message"]}
                onChange={(e) => changeHandler(e)}
              ></textarea>
            </div>
            <button style={btnStyle} onClick={() => console.log(details)}>
              Send Message
            </button>
          </div>
        </div>
        <div className="social_link_con">
          <Link to="/">Instagram</Link>
          <Link to="/">Facebook</Link>
          <Link to="/">LinkedIn</Link>
          <Link to="/">Twitter</Link>
        </div>
      </div>

      <div
        style={{
          background: "var(--main-color)",
          marginTop: "50px",
          minHeight: "calc(100vh - 55px)",
        }}
      >
        <h2 className="our-hub-head">OUR HUBS</h2>
        <div className="contact-info">
          {contactInfoContent.map(
            ({ place, address, email, phone, webisite }) => (
              <div className="content" key={Math.random()}>
                <h1 style={{ textTransform: "uppercase" }}>{place}</h1>
                <div>
                  {address.map((addr) => (
                    <p key={Math.random()}>{addr}</p>
                  ))}
                </div>
                <div className="content-email">
                  <a href={`mailto:${EMAIL}`} className="emailStyle">
                    <span></span> {email}
                  </a>
                  <br />
                  <a href={WEBSITE} target="_blank" rel="noopener noreferrer">
                    {webisite}
                  </a>
                </div>
                {/* <div className="content-email">
                                {place == "uk" ?
                                    <span>Mobile: +44 7928915493</span>
                                    :
                                    <>
                                        <span>Phone:  {CONTACT}</span> <br></br>
                                        <span>Mobile: {PHONE}</span>
                                    </>
                                }
                            </div> */}
              </div>
            )
          )}
        </div>

        <div style={{ width: "80%", margin: "0 auto", paddingBottom: "25px" }}>
          <Link
            to="/legal/links"
            style={{
              textTransform: "uppercase",
              color: "#fff",
              fontWeight: "bold",
              fontSize: "14px",
              marginRight: "20px",
            }}
          >
            Legal links
          </Link>
        </div>
      </div>
    </div>
  );
};

const contaninerStyle = {
  width: "100%",
  background: "#eee",
};

const mailIcon = {
  position: "absolute",
  top: "0",
  left: "-65px",
  background: "var(--main-color)",
  color: "#fff",
  padding: "30px 25px",
};

const paraContainer = {
  margin: "20px 0",
  fontSize: "15px",
  color: "#555",
};

const inputConStyle = {
  display: "flex",
  flexDirection: "column",
  marginBottom: "20px",
};

const labelStyle = {
  marginBottom: "10px",
  fontSize: "13px",
  color: "#444",
};

const inputField = {
  border: "none",
  borderBottom: "1px solid #000",
};

const textareaStyle = {
  border: "1px solid #000",
  padding: "10px",
  height: "100px",
  overflowY: "auto",
};

const btnStyle = {
  background: "var(--main-color)",
  color: "#fff",
  padding: "10px 20px",
  margin: "10px 0 0",
  border: "none",
  textTransform: "uppercase",
};

export default Footer;
