import React, { useEffect, useState, useRef } from 'react';
import DeptCompView from './DeptCompView';
import DeptMobView from './DeptMobView';

const Department = () => {

    const containerRef = useRef();
    const [mobileView, setMobileView] = useState(false);

    useEffect(() => {

        function screenResize() {
            if (window.innerWidth < 940)
                setMobileView(true);
            else
                setMobileView(false);
        }

        window.addEventListener('resize', screenResize);
        window.addEventListener('load', screenResize);

        return () => {
            window.removeEventListener('resize', screenResize);
            window.removeEventListener('load', screenResize);
        }

    }, [mobileView]);

    return (
        <div id="departments" ref={containerRef} className='department_main_container'>
            <div
                style={{ paddingTop: '100px' }}
                className='section-content-contaniner'
            >
                <span style={{ color: '#777', borderBottom: '2px solid var(--main-color)', paddingBottom: '4px' }}>departments</span>
            </div>
            { !mobileView ? <DeptCompView containerRef={containerRef} /> : <DeptMobView />}
        </div>
    )
}

export default Department;
