import React, { Fragment, useEffect } from 'react'
import { departmentContent } from '../../utils/exportContent'

const DeptMobView = () => {

    useEffect(() => {

        function scrollAppear() {
            const screenHeight = window.innerHeight / 1.5
            const contents = document.querySelectorAll('.department-mob-content')
            contents.forEach(content => {
                const contentPosition = content.getBoundingClientRect().top
                if (screenHeight > contentPosition)
                    content.classList.add('reveal-class')
            })
        }

        window.addEventListener('scroll', scrollAppear)
        window.addEventListener('load', scrollAppear)

        return () => {
            window.removeEventListener('scroll', scrollAppear)
            window.removeEventListener('load', scrollAppear)
        }

    }, [])

    return (
        <Fragment>
            {
                departmentContent.map(({ head, list }) => (
                    <div key={Math.random()} className='department-mob-content'>
                        <h1>{head}</h1>
                        <ul>{
                            list.map((d, index) => { return <li key={index} style={{ padding: '0.25em 0' }}>{d}</li> })
                        }</ul>
                    </div>
                ))
            }
        </Fragment>
    )
}

export default DeptMobView
