import React, { useEffect } from 'react';
import { servicesSectiontext, sectionHeadContent } from '../../utils/exportContent';
import SectionHead from '../layouts/SectionHead';

const OurServices = () => {

    useEffect(() => {
        function scrollAppear() {
            const contents = document.querySelectorAll('.our_services_grid'); 
            const screenHeight = window.innerHeight / 1.5;
            const contentPos1 = contents[0].getBoundingClientRect().top;
            const contentPos2 = contents[1].getBoundingClientRect().top;
            const contentPos3 = contents[2].getBoundingClientRect().top;
            const contentPos4 = contents[3].getBoundingClientRect().top;

            if (contentPos1 < screenHeight)
                contents[0].classList.add('reveal-class');
            if (contentPos2 < screenHeight)
                contents[1].classList.add('reveal-class');
            if (contentPos3 < screenHeight)
                contents[2].classList.add('reveal-class');
            if (contentPos4 < screenHeight)
                contents[3].classList.add('reveal-class');
        }
        window.addEventListener('scroll', scrollAppear);
        window.addEventListener('load', scrollAppear);

        return () => {
            window.removeEventListener('scroll', scrollAppear);
            window.removeEventListener('load', scrollAppear);
        }

    }, []);

    return (
        <div id="services" className='section-content-contaniner'>
            <SectionHead 
                section={sectionHeadContent[2].section} 
                head={sectionHeadContent[2].head} 
                para={sectionHeadContent[2].para} 
            />
            <div className='grid_container_services'>
                {
                    servicesSectiontext.map(({ text, head, source, back }) => (
                        <div key={Math.random()} style={{ "--back": back }} className='our_services_grid'>
                            <img 
                                style={{ objectFit: "cover", width: "100%", height: '100%', opacity: '0.4' }} 
                                src={source} 
                                alt="services" 
                            />
                            <h2>{head}</h2>
                            <div className='content'>{text}</div>
                        </div>
                    ))
                }
            </div>
        </div>
    )
}

export default OurServices;
