import React, { useEffect } from 'react';
import { aboutSectionText, sectionHeadContent } from '../../utils/exportContent';
import { motion } from 'framer-motion';
import SectionHead from './SectionHead';

const divCover = {
    rest: {
        width: "70px",
        height: "70px",
        x: "0",
        y: "0",
        borderRadius: '50%',
        transition: {
            duration: 2,
            type: "tween",
            ease: "easeIn"
        }
    },
    hover: {
        width: "100%",
        height: "calc(100% + 10px)",
        x: "-30%",
        y: "10%",
        borderRadius: 0,
        transition: {
            duration: 0.4,
            type: "tween",
            ease: "easeOut"
        }
    }
};

const About = () => {

    useEffect(() => {
        function scrollAppear() {
            const container = document.querySelector('.about_container');
            const screenHeight = window.innerHeight / 1.5;
            const containerPosition = container.getBoundingClientRect().top;
            if (containerPosition < screenHeight);
            container.classList.add('reveal-class');
        }
        window.addEventListener('scroll', scrollAppear);
        window.addEventListener('load', scrollAppear);

        return () => {
            window.removeEventListener('scroll', scrollAppear);
            window.removeEventListener('load', scrollAppear);
        }
    }, []);

    return (
        <div id='about' className='section-content-contaniner'>
            <div style={{ width: '100%', height: '80px' }}></div>
            <SectionHead
                section={sectionHeadContent[0].section}
                head={sectionHeadContent[0].head}
                para={sectionHeadContent[0].para}
            />
            <div className='about_container'>
                {
                    aboutSectionText.map(({ text, head, source, back }) => (
                        <motion.div
                            key={Math.random()}
                            initial="rest"
                            whileHover="hover"
                            animate="rest"
                            className='about_container_content'
                        >
                            <img src={source} alt="svg" />
                            <h3>{head}</h3>
                            <p>{text}</p>
                            <motion.div
                                variants={divCover}
                                className='about-circle'
                                style={{ "--back": back }}
                            ></motion.div>
                        </motion.div>
                    ))
                }
            </div>
        </div>
    )
}

export default About;
