import React, { useEffect } from 'react';
import SectionHead from './SectionHead';
import { sectionHeadContent, valuesContent } from '../../utils/exportContent';

const Views = () => {

    useEffect(() => {
        function scrollAppear() {
            const contents = document.querySelectorAll('.views_container .content');
            const screenHeight = window.innerHeight / 1.5;
            contents.forEach(content => {
                const contentPos = content.getBoundingClientRect().top;
                if (contentPos < screenHeight)
                    content.classList.add('reveal-class');
            })
        }
        window.addEventListener('scroll', scrollAppear);
        window.addEventListener('load', scrollAppear);

        return () => {
            window.removeEventListener('scroll', scrollAppear);
            window.removeEventListener('load', scrollAppear);
        }
    }, []);

    return (
        <div id="values" className='section-content-contaniner'>
            <div style={{ width: '100%', height: '80px' }}></div>
            <div className='views_container'>
                <SectionHead
                    section={sectionHeadContent[5].section}
                    head={sectionHeadContent[5].head}
                    para={sectionHeadContent[5].para}
                />
                <div className='content-con'>
                    {
                        valuesContent.map(({ source, head, content }) => (
                            <div className='content' key={Math.random()}>
                                <img style={{ width: "60px" }} src={source} alt='view' />
                                <h3 style={{ margin: "20px 0 10px" }}>{head}</h3>
                                <p>{content}</p>
                            </div>
                        ))
                    }
                </div>
            </div>
            <div style={{ width: '100%', height: '80px' }}></div>
        </div>
    )
}

export default Views;
