import React, { useState, useEffect } from 'react'
import { motion, AnimatePresence } from 'framer-motion'
import { landingHeadingText } from '../../utils/exportContent'

const sideLeftVariant = {
    initial: {
        scaleX: 0,
        originX: 1
    },
    final: {
        scaleX: [0, 1.3, 1],
        transition: {
            duration: 0.8,
            delay: 0.5,
            type: 'tween',
            ease: 'easeInOut'
        }
    }
}

const smallBarVariant = {
    initial: {
        scaleX: 0,
        originX: 1
    },
    final: {
        scaleX: [0, 1.3, 1],
        transition: {
            duration: 0.8,
            delay: 0.5,
            type: 'tween',
            ease: 'easeInOut'
        }
    }
}

const heading1Variant = {
    initial: {
        y: 20,
        opacity: 0
    },
    final: {
        y: 0,
        opacity: 1,
        transition: {
            duration: 1.5,
            delay: 0.2,
            type: 'tween'
        }
    }
}

const heading2Variant = {
    initial: {
        y: 20,
        opacity: 0
    },
    final: {
        y: 0,
        opacity: 1,
        transition: {
            duration: 0.8,
            delay: 0.4,
            type: 'tween'
        }
    },
    exit: {
        opacity: 0,
        duration: 1
    }
}

const paraVariant = {
    initial: {
        opacity: 0
    },
    final: {
        opacity: 1,
        transition: {
            duration: 0.75,
            delay: 2,
            type: 'tween'
        }
    },
}

const LandPage = () => {

    const [currentIndex, setCurrentIndex] = useState(0)
    const [reveal, setReveal] = useState(true)

    useEffect(() => {
        let interval = setInterval(() => {
            setReveal(false)
            let indexTxt = currentIndex === landingHeadingText.length - 1 ? 0 : currentIndex + 1
            setCurrentIndex(indexTxt)
            setTimeout(() => setReveal(true), 500)
        }, 4000)

        return () => {
            clearInterval(interval)
        }
    }, [currentIndex])

    return (
        <div className='land-page'>
            <div className='side-left'>
                <div>
                    <motion.h1
                        style={{ textTransform: 'uppercase' }}
                        variants={heading1Variant}
                        initial='initial'
                        animate='final'
                    >We build startups that are
                    </motion.h1>

                    <div style={{ height: '60px', width: '100%', overflow: 'hidden' }}>
                        <AnimatePresence>
                            {reveal && <motion.h1
                                style={{ textTransform: 'uppercase', color: landingHeadingText[currentIndex]['color'] }}
                                variants={heading2Variant}
                                initial="initial"
                                animate="final"
                                exit='exit'
                            >{landingHeadingText[currentIndex]['text']}</motion.h1>
                            }
                        </AnimatePresence>
                    </div>

                    <motion.div
                        style={smallBarStyle}
                        variants={smallBarVariant}
                        initial='initial'
                        animate='final'
                    ></motion.div>
                    <motion.p
                        variants={paraVariant}
                        initial='initial'
                        animate='final'
                        style={{ textTransform: 'uppercase' }}
                    >creating great products and experiences that have lasting impact</motion.p>
                </div>
            </div>

            <motion.div
                className='side-right'
                variants={sideLeftVariant}
                initial='initial'
                animate='final'
            ></motion.div>
        </div>
    )
}

const smallBarStyle = {
    width: '120px',
    height: '5px',
    background: 'var(--main-color)',
    marginTop: '2px',
    marginBottom: '16px',
    borderRadius: '10px'
}

export default LandPage
