import React, { useEffect } from 'react';
import { productContent, sectionHeadContent } from '../../utils/exportContent';
import { Link } from 'react-router-dom';
import SectionHead from '../layouts/SectionHead';
import Tilt from 'react-parallax-tilt';

const Products = () => {

    useEffect(() => {
        function scrollAppear() {
            const contents = document.querySelectorAll('.product_content');
            const screenHeight = window.innerHeight / 1.5;
            contents.forEach(content => {
                const contentPos = content.getBoundingClientRect().top;
                if (contentPos < screenHeight) 
                    content.classList.add('product_content_reveal');
            })
        }
        window.addEventListener('scroll', scrollAppear);
        window.addEventListener('load', scrollAppear);

        return () => {
            window.removeEventListener('scroll', scrollAppear);
            window.removeEventListener('load', scrollAppear);
        };
    }, []);

    return (
        <div id='products' className='section-content-contaniner'>
            <SectionHead 
                section={sectionHeadContent[1].section} 
                head={sectionHeadContent[1].head} 
                para={sectionHeadContent[1].para} 
            />
            {
                productContent.map(({ title, source, direction, link, des }) => (
                    <div 
                        className=
                            {
                                direction ? 
                                    'product_content product_content_row_reverse' : 
                                    'product_content product_content_row'
                            } 
                            key={Math.random()}>
                        <Tilt perspective='3000'>
                            <img src={source} alt='product' />
                        </Tilt>
                        <div className='content_container'>
                            <div>
                                <h1>{title}</h1>
                                <p>{des}</p>
                                <Link className='go-to-project-link' to={link}>
                                    See Details {' '}
                                    <i className="fa fa-long-arrow-right" aria-hidden="true"></i>
                                </Link>
                            </div>
                        </div>
                    </div>
                ))
            }
        </div>
    )
}

export default Products;
