import React, { useRef, useEffect } from 'react'
import { motion, AnimatePresence } from 'framer-motion'
const smallBarVariant = {
    initial: {
        scaleX: 0,
        originX: 1
    },
    final: {
        scaleX: [0, 1.3, 1],
        transition: {
            duration: 0.8,
            delay: 0.5,
            type: 'tween',
            ease: 'easeInOut'
        }
    }
}
const smallBarStyle = {
    width: '80px',
    height: '4px',
    background: 'var(--main-color)',
    margin: '6px 0',
    borderRadius: '10px'
}

const SectionHead = ({ section, head, para }) => {
    const containerRef = useRef()

    useEffect(() => {
        function scrollAppear() {
            const containerPos = containerRef.current.getBoundingClientRect().top
            const screenHeight = window.innerHeight / 1.5
            if (screenHeight > containerPos)
                containerRef.current.classList.add('section-head-con-reveal')
        }
        window.addEventListener('scroll', scrollAppear)
        window.addEventListener('load', scrollAppear)

        return () => {
            window.removeEventListener('scroll', scrollAppear)
            window.removeEventListener('load', scrollAppear)
        }
    })

    return (
        <div ref={containerRef} className='section-head-con'>
            { section && <span style={{ color: '#777', borderBottom: '2px solid var(--main-color)', paddingBottom: '4px' }}>{section}</span>}

            <h1 style={{ textTransform: 'uppercase' }}>{head}</h1>
            {para && <p>{para}</p>}
        </div>
    )
}

export default SectionHead
