import React, { useEffect, Fragment } from 'react'
import { departmentContent } from '../../utils/exportContent'

const DeptCompView = ({ containerRef }) => {

    // useEffect for revealing the department links
    useEffect(() => {
        function scrollAppear() {
            const screenHeight = window.innerHeight / 2
            const containerTop = containerRef.current.getBoundingClientRect().top
            if (screenHeight > containerTop) {
                const departments = document.querySelectorAll('.department')
                departments.forEach(department => {
                    department.classList.add('reveal-class')
                    setTimeout(() => {
                        department.style.transition = 'all 0.5s ease'
                        department.style.transitionDelay = '0'
                    })
                })
            }
        }

        window.addEventListener('scroll', scrollAppear)
        window.addEventListener('load', scrollAppear)

        return () => {
            window.removeEventListener('scroll', scrollAppear)
            window.removeEventListener('load', scrollAppear)
        }

    })

    // useEffect for the mouse events on the deparments
    useEffect(() => {
        const departments = document.querySelectorAll('.department')
        const contents = document.querySelectorAll('.department_main_container .content')

        departments.forEach((department, index) => {

            // mouseover event
            department.addEventListener('mouseover', e => {
                containerRef.current.style.backgroundColor = e.target.getAttribute('data-background-color')
                contents[index].classList.add('content-reveal')
            })

            // mouseout event
            department.addEventListener('mouseout', () => {
                containerRef.current.style.backgroundColor = '#f1f1f1'
                contents[index].classList.remove('content-reveal')
            })
        })

    })

    return (
        <Fragment>
            <div className='headings_container'>
                {departmentContent.map(({ head, back }, index) =>
                    <p
                        key={Math.random()}
                        style={{ "--delay": `${index * 0.2}s` }}
                        data-background-color={back}
                        className='department'>{head}
                    </p>
                )}
            </div>
            <div>
                {
                    departmentContent.map(({ list }) => (
                        <div key={Math.random()} className='content'>
                            <ul>{
                                list.map((d, index) => { return <li key={index} style={{ padding: '0.35em 0' }} className="listStyle"> {d}</li> })
                            }</ul>
                        </div>
                    ))
                }
            </div >
        </Fragment >
    )
}

export default DeptCompView
